.block-intro {
  padding: 50px 0px 16px 0px;
  @media (max-width: 768px) {
    padding: 98px 0px 0px 0px;
  }
  // .block-intro__content

  &__content {
    position: relative;
  }

  // .block-intro__top

  &__top {
   
  }

  // .block-intro__bottom

  &__bottom {
  }

  // .block-intro__bg

  &__bg {
    position: absolute;
    top: -291px;
    left: 50%;
    transform: translate(-50%, 0px);
    width: auto;
    height: calc(100% + 215px);
    z-index: 1;
    @media (max-width: 1370px) {
      top: -251px;
      left: auto;
      right: -350px;
      transform: translate(0px, 0px);
      height: calc(100% + 251px);
    }
    @media (max-width: 1200px) {
      top: -200px;
      height: calc(100% + 200px);
    }
    @media (max-width: 992px) {
      top: -100px;
      left: -34px;
      width: calc(100% + 74px);
      transform: rotate(-45deg);
      height: auto;
    }
    @media (max-width: 768px) {
      top: -550px;
    }
    @media (max-width: 700px) {
      top: -480px;
    }
    @media (max-width: 600px) {
      top: -380px;
    }
    @media (max-width: 480px) {
      top: -350px;
    }
    @media (max-width: 400px) {
      top: -280px;
    }
  }
}
.top-block-intro {
  position: relative;
  z-index: 10;
  display: flex;
  justify-content: space-between;
  gap: 30px;
  @media (max-width: 992px) {
    flex-direction: column;
    justify-content: start;
    gap: 20px;
  }

  // .top-block-intro__body

  &__body {
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    .title1 {
      flex: 1 1 auto;
      margin: 0px 0px 22px -9px;
      @media (max-width: 768px) {
        margin: 0px 0px 10px 0px;
      }
    }
    p {
      max-width: 363px;
      font-weight: 300;
      font-size: 36px;
      line-height: 130%;
      text-transform: uppercase;
      color: var(--darkgrey);
      @media (max-width: 768px) {
        font-size: 24px;
        br {
          display: none;
        }
      }
    }
  }

  // .top-block-intro__image

  &__image {
    height: 346px;
    margin: 10px 0px 0px 0px;
    img {
      border-radius: 30px;
      width: auto;
      height: 100%;
    }
    @media (max-width: 992px) {
      width: 100%;
      height: 100%;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
      }
    }
  }
}
.bottom-block-intro {
  position: relative;
  z-index: 10;
  margin: 37px 0px 0px 0px;
  @media (max-width: 768px) {
    margin: 20px 0px 0px 0px;
  }
  // .bottom-block-intro__slider

  &__slider {
    @media (max-width: 1370px) {
      margin: 0px -15px;
      padding: 0px 15px;
    }
  }

  // .bottom-block-intro__wrapper

  &__wrapper {
    @media (max-width: 768px) {
      display: grid;
      gap: 10px;
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // .bottom-block-intro__slide

  &__slide {
    width: 207px;
    display: flex;
    height: auto;
    &:first-child {
      width: 433px;
    }
    @media (max-width: 768px) {
      width: 100%;
      &:first-child {
        width: 100%;
        grid-column: 1 / -1;
      }
    }
  }

  // .bottom-block-intro__item

  &__item {
    width: 100%;
    position: relative;
    height: 165px;
    border-radius: 30px;
    overflow: hidden;
    background-color: var(--brown);
    @media (max-width: 480px) {
      height: 131px;
      border-radius: 20px;
    }
    video {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  // .bottom-block-intro__text

  &__text {
    padding: 15px;
    position: relative;
    height: 100%;
    z-index: 10;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    @media (max-width: 480px) {
      padding: 10px;
    }
    .btn-circle {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  // .bottom-block-intro__title

  &__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--white);
    @media (max-width: 480px) {
      font-size: 19px;
    }
  }

  // .bottom-block-intro__arrow-prev

  &__arrow-prev {
    position: absolute;
    right: 60px;
    top: -75px;
    z-index: 20;
    &:hover {
      background-color: var(--black);
      svg {
        fill: var(--white);
      }
    }
    @media (max-width: 768px) {
      display: none;
    }
  }

  // .bottom-block-intro__arrow-next

  &__arrow-next {
    position: absolute;
    right: 0;
    top: -75px;
    &:hover {
      background-color: var(--black);
      svg {
        fill: var(--white);
      }
    }
    @media (max-width: 768px) {
      display: none;
    }
  }
}

.block {
  position: relative;
  z-index: 10;
  // .block__content

  &__content {
    position: relative;
    display: flex;
    justify-content: space-between;
    background: var(--white);
    border-radius: 40px;
    padding: 30px;
    gap: 30px;
    min-height: 470px;
    overflow: hidden;
    @media (max-width: 992px) {
      flex-direction: column;
      justify-content: start;
      padding: 20px;
      gap: 20px;
      min-height: 100%;
      border-radius: 20px;
    }
    @media (max-width: 375px) {
      padding: 10px;
    }
  }

  // .block__left

  &__left {
    flex: 0 1 auto;
    display: flex;
    align-items: start;
    flex-direction: column;
    justify-content: space-between;
    gap: 30px;
    .btn {
      font-size: 20px;
    }
    @media (max-width: 992px) {
      flex-direction: row;
      gap: 15px;
    }
  }

  // .block__center

  &__center {
    position: absolute;
    top: 50%;
    transform: translate(0px, -50%);
    left: 0;
    overflow: hidden;
    display: flex;
    gap: 50px;
    @media (max-width: 992px) {
      position: relative;
      top: 0;
      transform: translate(0px, 0px);
      margin: 0px -20px;
      width: calc(100% + 40px);
    }
    @media (max-width: 375px) {
      margin: 0px -10px;
      width: calc(100% + 20px);
    }
    p {
      animation: marquee-main 50s infinite linear;
      font-weight: 700;
      font-size: 80px;
      line-height: 130%;
      text-transform: uppercase;
      color: var(--brown);
      white-space: nowrap;
      @media (max-width: 768px) {
        font-size: 40px;
      }
    }
  }

  // .block__right

  &__right {
    position: relative;
    flex: 0 1 464px;
    padding: 20px;
    background: var(--black);
    border-radius: 25px;
    display: flex;
    flex-direction: column;
    gap: 50px;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
    @media (max-width: 768px) {
      padding: 15px;
    }
    .btn-circle-orange {
      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
      }
      &:hover {
        svg {
          fill: var(--brown);
        }
      }
    }
    &:hover {
      .block__pic img {
        transform: scale(1.05);
      }
    }
  }

  // .block__titles

  &__titles {
    display: flex;
    justify-content: space-between;
    gap: 30px;
    flex: 1 1 auto;
    @media (max-width: 768px) {
      gap: 20px;
    }
  }

  // .block__title

  &__title {
    font-weight: 300;
    max-width: 400px;
    font-size: 24px;
    line-height: 130%;
    text-transform: uppercase;
    color: var(--white);
  }

  // .block__pic

  &__pic {
    width: 100%;
    height: auto;
    border-radius: 20px;
    min-height: 189px;
    overflow: hidden;
    img {
      transition: transform 0.5s;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}
@keyframes marquee-main {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-50% - 10px));
  }
}

.block-forms {
  // .block-forms__content

  &__content {
    position: relative;
    background-color: var(--black);
    border-radius: 40px;
    padding: 30px;
    overflow: hidden;
    display: flex;
    align-items: start;
    gap: 50px;
    form {
      width: calc(100% - 510px);
      display: flex;
      flex-direction: column;
      gap: 30px;
      @media (max-width: 1200px) {
        width: calc(100% - 450px);
      }
      @media (max-width: 992px) {
        width: 100%;
      }
      @media (max-width: 768px) {
        gap: 20px;
      }
    }
    .checkboxes {
      margin: -15px 0px 0px 0px;
      @media (max-width: 768px) {
        margin: -5px 0px 0px 0px;
      }
    }
    @media (max-width: 1370px) {
      gap: 30px;
    }
    @media (max-width: 1200px) {
      padding: 30px 20px;
    }
    @media (max-width: 992px) {
      padding: 20px;
    }
  }

  // .block-forms__top

  &__top {
    display: flex;
    flex-direction: column;
    gap: 30px;
    @media (max-width: 768px) {
      gap: 20px;
    }
    .title2 {
      color: var(--white);
    }
    ul {
      display: flex;
      flex-direction: column;
      gap: 10px;
      li {
        display: flex;
        font-weight: 300;
        font-size: 24px;
        line-height: 130%;
        text-transform: uppercase;
        color: var(--white);
        &::before {
          content: "";
          width: 6px;
          height: 6px;
          border-radius: 50%;
          background-color: var(--white);
          flex: 0 0 6px;
          margin: 13px 7px 0px 0px;
        }
      }
    }
  }

  // .block-forms__bottom

  &__bottom {
    display: flex;
    align-items: start;
    gap: 10px;
    margin: 0px 34px 0px 0px;
    @media (max-width: 1370px) {
      margin: 0px 0px 0px 0px;
    }
    @media (max-width: 768px) {
      flex-direction: column;
      width: 100%;
    }
    .btn {
      min-height: 68px;
      justify-content: center;
      transition:
        background-color 0.3s,
        color 0.3s;
      &:hover {
        background-color: var(--white);
        color: var(--brown);
      }
      @media (max-width: 768px) {
        width: 100%;
        font-size: 20px;
      }
    }
  }

  // .block-forms__inputs

  &__inputs {
    flex: 1 1 auto;
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      width: 100%;
    }
  }

  // .block-forms__input

  &__input {
    background-color: var(--white);
    border-radius: 20px;
    padding: 20px;
    height: 68px;
    position: relative;
    input {
      font-weight: 300;
      font-size: 19px;
      line-height: 130%;
      width: 100%;
      &::placeholder {
        opacity: 0;
        transition: opacity 0.3s;
        color: var(--red);
      }
    }
    &._form-error {
      border: 1px solid var(--glow-red);
      input {
        &::placeholder {
          opacity: 1;
        }
      }
      .block-forms__placeholder {
        opacity: 0;
      }
    }
    &._form-focus {
      .block-forms__placeholder {
        opacity: 0;
      }
    }
    &._filled {
      .block-forms__placeholder {
        opacity: 0;
      }
    }
  }

  // .block-forms__placeholder

  &__placeholder {
    transition: opacity 0.3s;
    position: absolute;
    top: 21px;
    left: 20px;
    width: 100%;
    height: auto;
    pointer-events: none;
    font-weight: 300;
    font-size: 19px;
    line-height: 130%;
    color: var(--middlegrey);
    span {
      color: var(--brown);
    }
  }

  // .block-forms__right

  &__right {
    position: absolute;
    width: 455px;
    top: 0;
    right: 30px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 10px;
    @media (max-width: 1200px) {
      width: 400px;
      right: 20px;
    }
    @media (max-width: 992px) {
      display: none;
    }
  }

  // .block-forms__columns

  &__columns {
    display: flex;
    flex-direction: column;
    gap: 7px;
    transition: transform 0.3s ease-out;
  }

  // .block-forms__column

  &__column {
    border-radius: 30px;
    overflow: hidden;
    background-color: var(--blackligth);
    height: 199px;
    @media (max-width: 1200px) {
      height: 180px;
    }
    img {
      height: 100%;
      width: auto;
      object-fit: cover;
    }
  }
}

.main-about {
  .block-intro {
    padding: 50px 0px 0px 0px;
    @media (max-width: 768px) {
      padding: 25px 0px 0px 0px;
    }
  }
  .top-block-intro__body {
    .title1 {
      font-size: 198px;
      @media (max-width: 1200px) {
        font-size: 180px;
      }
      @media (max-width: 992px) {
        font-size: 140px;
      }
      @media (max-width: 768px) {
        font-size: 80px;
      }
      @media (max-width: 480px) {
        font-size: 55px;
      }
    }
  }
  .block-intro__bg {
    left: 39%;
    transform: translate(-50%, 0px);
    height: calc(100% + 290px);
    @media (max-width: 992px) {
      top: -100px;
      left: -74px;
      width: calc(100% + 74px);
      transform: rotate(-45deg);
      height: auto;
    }
    @media (max-width: 600px) {
      top: -380px;
    }
    @media (max-width: 480px) {
      top: -280px;
      left: -55px;
      width: 100%;
    }
  }
}

.block-about {
  // .block-about__content

  &__content {
    background-color: var(--white);
    border-radius: 40px;
    padding: 30px;
    display: flex;
    gap: 20px;
    @media (max-width: 992px) {
      flex-direction: column;
      padding: 20px;
      border-radius: 20px;
    }
  }

  // .block-about__image

  &__image {
    flex: 0 1 434px;
    border-radius: 25px;
    overflow: hidden;
    position: relative;
    @media (max-width: 992px) {
      flex: 1 1 auto;
      border-radius: 15px;
    }
  }

  // .block-about__pic1

  &__pic1 {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  // .block-about__pic2

  &__pic2 {
    position: absolute;
    bottom: -9px;
    right: -9px;
    border: 9px solid var(--white);
    border-radius: 25px;
    width: 140px;
    height: 140px;
  }

  // .block-about__body

  &__body {
    flex: 0 1 825px;
    gap: 20px;
    display: flex;
    flex-direction: column;
    @media (max-width: 992px) {
      flex: 1 1 auto;
    }
  }

  // .block-about__titles

  &__titles {
    display: flex;
    align-items: start;
    gap: 20px;
    @media (max-width: 768px) {
      flex-direction: column-reverse;
      align-items: flex-end;
    }
    .title2 {
      flex: 1 1 auto;
    }
    svg {
      flex: 0 0 65px;
      fill: var(--brown);
      @media (max-width: 768px) {
        flex: 1 1 auto;
      }
    }
  }

  // .block-about__subtitle

  &__subtitle {
    display: flex;
    align-items: start;
    p {
      font-weight: 300;
      font-size: 24px;
      line-height: 130%;
      text-transform: uppercase;
      max-width: 594px;
    }
    img {
      margin: -20px 0px 0px 0px;
      width: 80px;
      height: 80px;
      transform: rotate(45deg);
    }
  }

  // .block-about__bottom

  &__bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    height: 100%;
    p {
      font-weight: 300;
      font-size: 24px;
      line-height: 130%;
      color: var(--brown);
    }
    img {
      margin: -20px 0px 0px 0px;
      width: 140px;
      height: 140px;
    }
  }
}
