.checkboxes {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.checkbox {
    position: relative;
    // .checkbox__input
    &__input {
        position: absolute;
        width: 0;
        height: 0;
        opacity: 0;
        &:checked + .checkbox__label .checkbox__icon svg {
            opacity: 1;
        }
        &._form-error + .checkbox__label .checkbox__icon {
            border: 1px solid var(--red);
        }
    }
    // .checkbox__label
    &__label {
        cursor: pointer;
        display: inline-flex;
        position: relative;
        gap: 14px;
    }
    // .checkbox__icon
    &__icon {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--white);
        border: 1px solid var(--ligthgrey);
        border-radius: 7px;
        width: 24px;
        height: 24px;
        flex: 0 0 24px;
        svg {
            opacity: 0;
            transition: opacity 0.3s;
            fill: var(--brown);
        }
    }
    // .checkbox__text
    &__text {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 140%;
        color: var(--middlegrey);
        a {
            text-decoration: underline;
            text-decoration-skip-ink: none;
            transition:
                color 0.3s,
                text-decoration 0.3s;
            &:hover {
                text-decoration: none;
                color: var(--brown);
            }
        }
    }
}
